.project-container {
  display: flex;
  border-radius: 10px;
  box-shadow: 0 50px 100px rgba(50, 50, 93, 0.05),
    0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.1);
  background-color: #002142;
}

.songs-container {
  display: flex;
  box-shadow: 0 50px 100px rgba(50, 50, 93, 0.05),
    0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.1);
  padding-bottom: 2rem;
  padding-top: 2rem;
  margin-top: 3rem;
}

.about-content {
  font-size: 1.1rem;
  padding-top: 1rem;
}

.about-header {
  font-weight: 400;
  font-size: 2.6em;
}

.card-img-top {
  height: 20rem;
  object-fit: cover;
}

.card-hori {
  display: flex;
  flex: 1 1 auto;
}

.img-wrapper {
  height: 10rem;
  object-fit: cover;
}

.card {
  width: 100%;
}

.apple-music-logo {
  display: inline-block;
  overflow: hidden;
  background: url(https://tools.applemusic.com/en-us/badge-lrg.svg?kind=playlist&bubble=apple_music)
    no-repeat;
  width: 158px;
  height: 45px;
  margin-top: 1rem;
}

.footer {
  background-color: #002142;
  color: #eea200;
  padding: 4rem;
  margin-top: 10rem;
}

.pdf-link,
.pdf-link:hover {
  color: #eea200;
}

.bg-theme {
  background-color: #002142;
}

.projects {
  background-color: #002142;
  margin-top: 7rem;
}

.songs {
  padding-top: 2rem;
}

#projects {
  color: #fff;
}

.languages {
  font-size: 16px;
}

iframe {
  width: 100%;
  height: 100%;
  min-height: 250px;
}

.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 1200ms ease-out, transform 600ms ease-out,
    visibility 1200ms ease-out;
  will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

.btn-project {
  display: inline-block;
  font-weight: 400;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  background-color: #eea200;
}

.btn-project:hover {
  color: #fff;
  text-decoration: none;
}

.navbar .navbar-nav .nav-item {
  margin-right: 15px;
  font-weight: 400;
  font-size: 1.2em;
  color: #002142;
}

html {
  scroll-behavior: smooth;
  font-family: sans-serif, Arial, Helvetica;
}

